<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <search-box @query="onQueryChange" @focus="showSearch"></search-box>
        </div>
        <div class="icon-wrapper">
          <i class="icon-category" @click="showCategory"></i>
        </div>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div class="list">
            <good-item
              class="item"
              v-for="(item,index) in list"
              :key="item.id"
              :item="item"
              @count="calc"
            >
            </good-item>
          </div>
        </scroll>
        <div class="settle-wrapper" v-show="total">
          <settle @submit="submit" :price="price" :total="total"></settle>
        </div>
      </div>
      <search :isShowSearch="isShowSearch" @hide="hideSearch"></search>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import GoodItem from 'base/good-item/good-item'
  import Scroll from 'base/scroll/scroll'
  import SearchBox from 'base/search-box/search-box'
  import Settle from 'base/settle/settle'
  import Search from 'components/purchase/search'
  import {mapActions} from 'vuex'

  export default {
    data () {
      return {
        query: '',
        list: [],
        isShowSearch: false,
        selectedGoods: {},
        total: 0,
        price: 0
      }
    },
    created () {
      this._getOrderList()
    },
    methods: {
      back() {
        this.$router.back()
      },
      showSearch() {
        this.isShowSearch = true
      },
      hideSearch() {
        this.isShowSearch = false
      },
      showCategory() {

      },
      _getOrderList() {
        for(let i = 0; i < 10; i++){
          this.list.push({
            id: i + 1,
            src: 'https://y.gtimg.cn/music/photo_new/T003R720x288M000000yTU5u2XJFWh.jpg?max_age=2592000',
            name:'可口可乐',
            desc: '600ml',
            price: parseInt(Math.random() * 10 + 1),
            unit: '瓶'
          })
        }
      },
      // 计算数量和价格
      calc(item){
        this.selectedGoods[item.id] = item
        let tempTotal = 0
        let tempPrice = 0
        for(let key in this.selectedGoods){
          let value = this.selectedGoods[key]
          tempTotal += value.count
          tempPrice += value.price * value.count
        }
        this.total = tempTotal
        this.price = tempPrice
        this.item = item
      },
      // 跳转到购物车模块
      submit(){
        this.setShoppingCart(this.selectedGoods)
        this.$router.push({
          path: `${this.$router.currentRoute.path}/shoppingcart`
        })
      },
      onQueryChange(newQuery) {
        this.query = newQuery
      },
      ...mapActions([
        'setShoppingCart'
      ])
    },
    components: {
      SearchBox,
      Search,
      Settle,
      GoodItem,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
      .icon-wrapper
        .icon-category
          position absolute
          background url("~common/image/purchase/category.png") no-repeat
          background-size contain
          width 14px
          height 13px
          right 15px
          top 16px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          .item
            height 110px
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px
</style>



